.timeline {
  position: relative;
  border-left: 1px solid rgba(120, 130, 140, 0.13);
}
.timeline-item {
  position: relative;
  border-color: rgba(120, 130, 140, 0.13);
  padding-bottom: 1px;
}
.timeline-item:before {
    content: '';
    position: absolute;
    border-color: inherit;
    border-width: 4px;
    border-style: solid;
    border-radius: 50%;
    width: 15px;
    height: 15px;
    margin-left: -8px;
    top: 2px;
    left: 0;
}
.timeline-item .timeline-item-main {
    margin-left: 24px;
    padding-bottom: 16px;
}
.timeline-item .timeline-icon {
    position: absolute;
    left: -10px;
    z-index: 1;
    width: 20px;
    height: 20px;
    border-width: 10px;
    background: #d2d6de;
    border-radius: 50%;
    text-align: center;
}
