.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}
@keyframes spinAround {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
}
}
ul {
  list-style: none;
}
.main {
  min-height: 50vh;
}
.header-icon {
  font-size: 26px;
}
.header-avatar {
  border-radius: 50%;
  overflow: hidden;
}
.header-avatar img {
    max-height: none;
}
@media screen and (min-width: 980px) {
.header > .nav {
    height: 80px;
}
}
.header .nav-left {
  -webkit-box-flex: 3;
      -ms-flex-positive: 3;
          flex-grow: 3;
}
.header .nav-left .nav-item {
    font-size: 18px;
    padding-left: 15px;
    padding-right: 15px;
    font-weight: 700;
}
.header .nav-left .nav-item.is-brand {
      padding-right: 40px;
}
.header .nav-left .nav-item.is-brand img {
        max-height: 50px;
}
@media screen and (max-width: 768px) {
.header .nav-left .nav-item.is-brand img {
            height: 44px;
}
}
.header .nav-item.is-badge {
  padding-right: 0;
  width: 80px;
